<script>
  export default {
    name: "login",
    data () {
      return {
        formInline: {
            user: '',
            password: ''
        },
        ruleInline: {
            user: [
                { required: true, message: '请填写用户名', trigger: 'blur' }
            ],
            password: [
                { required: true, message: '请填写密码', trigger: 'blur' },
                { type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
            ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        let _this = this;
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Notice.success({
              title: '登录成功!'
            });
            _this.$router.push('/index');
          } else {
            this.$Notice.error({
              title: '请输入正确的用户名或密码!'
            });
          }
        })
      }
    }
  }
</script>
<template>
  <div id="login">
    <div class="login_head_tlt">BMS后台管理系统</div>
    <div class="login_head_bg"></div>
    <div class="login_body">
      <div class="login_body_l"></div>
      <div class="login_body_r">
        <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
          <Form-item prop="user">
            <Input type="text" class="admin" v-model="formInline.user" placeholder="请输入用户名"></Input>
          </Form-item>
          <Form-item prop="password">
            <Input type="password" class="pass" v-model="formInline.password" placeholder="请输入用户密码"> </Input>
          </Form-item>
          <Form-item>
            <Button type="primary" class="login_in" @click="handleSubmit('formInline')">登录</Button>
          </Form-item>
        </Form>
      </div>
    </div>
  </div>
</template>
<style scoped>
#login {
  width: 100vw;
  height: 100vh;
  background: url(../../assets/bj.jpg) no-repeat center;
}
.login_head_tlt {
  width: 950px;
  height: 70px;
  line-height: 130px;
  text-align: center;
  color: #fff;
  font-size: 32px;
  text-shadow: 0 0 0.3rem #00d8ff;
  margin: auto;
}
.login_head_bg {
  width: 950px;
  height: 95px;
  line-height: 95px;
  margin: auto;
  background: url(../../assets/head.gif) no-repeat center;
  background-size: cover;
}
.login_body {
  width: 860px;
  height: 600px;
  margin: 80px auto 0;
}
.login_body .login_body_l {
  float: left;
  width: 440px;
  height: 406px;
  background: url(../../assets/d.png) no-repeat center;
  background-size: cover;
  animation: rotation 15s linear infinite;
}
@-webkit-keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
    }
}
.login_body .login_body_r {
  float: right;
  width: 388px;
  height: 396px;
  background: url(../../assets/e.png) no-repeat center;
  background-size: cover;
  padding-top: 94px;
}
.login_body .login_body_r .admin, .login_body .login_body_r .pass {
  display: block;
  text-indent: 20px;
  width: 316px;
  height: 46px;
  border: none;
  padding: 10px;
  background: url(../../assets/b.png) no-repeat center;
  background-size: cover;
  margin: 20px auto;
  outline: none;
  color: #fff;
}
.login_body .login_body_r .login_in {
  display: block;
  width: 316px;
  height: 46px;
  border: none;
  padding: 10px;
  background: url(../../assets/a.png) no-repeat center;
  background-size: cover;
  margin: 20px auto;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
}
.ivu-form-inline .ivu-form-item {
  display: block;
  margin: 0;
}
</style>